<!-- 蓝色简洁登录页面 -->
<template>
	<view class="login-bg theme-radiation-solid-bg ">
		<image class="img-a" src="@/static/image/login/bg1.png"></image>
		<!-- #ifndef MP-WEIXIN -->
		<view class="logo">
			<view class="img">
				<image mode="widthFix"
					src="data:image/png;base64,">
				</image>
			</view>
		</view>
		<view class="t-login theme-btn">
			<view class="t-b">欢 迎 您</view>
			<form class="cl">
				<view class="t-a">
					<input type="number" name="phone" placeholder="请输入手机号码" maxlength="11" v-model="phone" />
				</view>
				<view class="t-a">
					<input type="password" name="code" maxlength="6" placeholder="请输入密码" v-model="pwd" />
				</view>
				<view class="t-c" @tap="forgotPwd()">忘记密码</view>
				<button @tap="loginWeixin()">登 录</button>
			</form>
		</view>
		<view class="cardBox">
			<view>
				还没有登录账号？
				<text class="txt" @tap="reg()">立刻注册</text>
			</view>
		</view>
		<!-- #endif -->
		<!-- #ifdef MP-WEIXIN -->
		<view class="t-login weixin theme-btn">
			<view class="logo-weixin">
				<view class="img boxshadow4">
					<image mode="widthFix"
						src="data:image/png;base64,">
					</image>
				</view>
			</view>
			<!-- <view class="t-b-weixin">一一 第 三 方 登 录 一一</view> -->
			<form class="cl">
				<button @tap="loginWeixin()">
					<i class="iconfont icon-weixin" style=" font-size: 36rpx;"></i>
					<view style="margin-left: 30rpx; font-size: 36rpx;">一 键 微 信 登 录</view>
				</button>
			</form>
		</view>
		<!-- #endif -->
		<image class="img-b" src="@/static/image/login/bg2.png"></image>
	</view>
</template>
<script>
	export default {
		data() {
			return {
				phone: '', //手机号码
				pwd: '' //密码
			};
		},
		onLoad() {},
		methods: {
			//当前登录按钮操作
			login() {
				var that = this;
				if (!that.phone) {
					uni.showToast({
						title: '请输入手机号',
						icon: 'none'
					});
					return;
				}
				if (!/^[1][3,4,5,7,8,9][0-9]{9}$/.test(that.phone)) {
					uni.showToast({
						title: '请输入正确手机号',
						icon: 'none'
					});
					return;
				}
				if (!that.pwd) {
					uni.showToast({
						title: '请输入密码',
						icon: 'none'
					});
					return;
				}
				uni.showToast({
					title: '登录成功！',
					icon: 'none'
				});
			},
			//忘记密码
			forgotPwd() {
				uni.showToast({
					title: '忘记密码',
					icon: 'none'
				});
			},
			//立刻注册
			reg() {
				uni.showToast({
					title: '立刻注册',
					icon: 'none'
				});
			},
			/**
			 * 微信登录
			 */
			loginWeixin() {
				this.tui.setUserInfo({
					id: 123,
					name: "张三",
					phone: '18712352145',
				});
				this.openPages.openIndexCategory();
			},
		}
	};
</script>
<style lang="scss" scoped>
	@import "login.scss";
</style>
